.logo {
    color: $white;
    display: block;
    max-width: 320px;
    font-size: 0;
    margin: 0 auto;

    &:hover {
        color: $white;
    }

    svg {
        width: 100%;
    }
}

.logo--small {
    width: 30px;
    svg {
        max-height: $base-spacing;
    }
}

.tagline {
    padding-top: $half-spacing;
    color: $blue;
    text-align: center;
    text-transform: uppercase;
    font-weight: bold;
    letter-spacing: 1px;
    font-size: .8em;

    @include media-query(min, $lap-start) {
        //text-align: left;
        font-size: 1.2em;
    }
}
